import { Component } from '@angular/core';
import {  ActionSheetController  ,AlertController,IonicPage,ModalController, NavController, NavParams , InfiniteScroll } from 'ionic-angular';
import { Auth ,NotificationProvider} from '../../../providers';
import { TranslateService } from '@ngx-translate/core';
import { Storage } from '@ionic/storage';

@IonicPage()
@Component({
  selector: 'page-conferencia-list',
  templateUrl: 'conferencia-list.html',
})
export class ConferenciaListPage {

  public loading: boolean = true;
  items: any=[];
  items2: any=[];
  generated = '';
  idempresa:any;
  idafiliado:any='na';
  idevento: any;
  nevento: any;
  ubica: any;
  fecha: any;
  pingreso:boolean=false;
  pconsulta:boolean=false;
  peliminacion:boolean=false;
  pcreacion:boolean=false;
  pactualizacion:boolean=false;
  preporte:boolean=false;
  pfiltro:boolean=false;
  permisom: any=[];
  public hasFilter: boolean = false;
  public gLimit: number = 50;
  public PageSegmet: string = 'foods';
  public gSearchQuery: string = '';
  username: string = "";

  constructor( public modalCtrl : ModalController,public actionSheetCtrl: ActionSheetController
    ,public storage: Storage,public alertCtrl: AlertController,public msj: NotificationProvider, public translateService: TranslateService  ,public navCtrl: NavController,
    private _MODAL  		: ModalController, public navParams: NavParams,
     public regService: Auth) {

      this.permisom = String(navParams.get("permiso")).split(",");
      this.pingreso=(  this.permisom[0] == 'true');
      this.pconsulta=(  this.permisom[1] == 'true');
      this.peliminacion=(  this.permisom[2] == 'true');
      this.pcreacion=(  this.permisom[3] == 'true');
      this.pactualizacion=(  this.permisom[4] == 'true');
      this.pfiltro=(  this.permisom[5] == 'true');
      this.preporte=(  this.permisom[6] == 'true');


      this.translateService.get('APPX').subscribe((value) => {
        this.idempresa=value
       
    })
  }
  
  public clearSearchg(): void {
    this.gSearchQuery = '';
  }

  ngOnInit() {
    this.storage.get('username').then((value) => {
      this.username = value;
  });   
  }



  pideregistros() { 
      let self = this;
      self.loading = true;
     
      this.regService.getReg(this.idevento + '/' + this.idempresa,'conferencias').then((data) => {
 
      this.items=data;
      this.items2=data;
      self.loading = false;
  }, (err) => {
    this.msj.showError3('Error',err.error);
  });
  }
  ionViewDidLoad() {
    this.idevento=this.navParams.data.record._id;
    this.nevento=this.navParams.data.record.nombre;
    this.ubica=this.navParams.data.record.ubicacion;
    this.fecha=this.navParams.data.record.fechaini;

    this.pideregistros();


  }
  
  public loadMore(ev: InfiniteScroll) {
    this.gLimit += 50;
    setTimeout(() => ev.complete(), 1000);
  }


  deleteRecord(item : any, no) : void
  {

    let alert = this.alertCtrl.create({ title: 'Esta seguro de realizar esta operaciÃ³n?',
    buttons: [
      {text: 'Si',
        handler: () => {
              
          let recordID 		: string		= item._id;
          this.regService.deleteReg(recordID+'/'+this.username+ '/' + this.idempresa + '/' + this.idafiliado,'conferencias').then((data) => {
              this.items.splice(no, 1);
              this.items = this.items.concat([]);
              this.items2=this.items
          }, (err) => {
            this.msj.showError3('Error',err.error);
          });
      
        }
      },
      {
        text: 'No',  handler: () => { }
      }
    ]
  });
  alert.present();


  
  
  }
  value:any;

  updateRecord(item : any,no) : void
   {
    let addModal = this._MODAL .create('ConferenciaCreatePage',  { record : item ,record2:{idevento:this.idevento,nidevento:this.nevento}},{showBackdrop: true,enableBackdropDismiss: false});
    addModal.onDidDismiss(data => {
      if (data) {
        this.items.splice(no, 1);
        this.items = this.items.concat([data]);
        this.items2=this.items
      }
    })
    addModal.present();
    //  this.navCtrl.push('EmpresaCreatePage', { record : item });
   }
   addRecord() : void
   {
    let addModal = this._MODAL .create('ConferenciaCreatePage', { record : null ,record2:{idevento:this.idevento,nidevento:this.nevento}},{showBackdrop: true,enableBackdropDismiss: false});
    addModal.onDidDismiss(data => {
      if (data) {
        this.items = this.items.concat([data]);
        this.items2=this.items
      }  
    })
    addModal.present();
      //this.navCtrl.push('EmpresaCreatePage');
   }
   viewRecord(item : any): void
   {
     
   }


   presentFilter() {
    
  }


}



